/* CSS Toggle Switch */
.switch {
  position: relative;
  display: inline-block;
  width: 26px;
  /* 32px * 0.8 ~= 26px */
  height: 15px;
  /* 18px * 0.8 ~= 15px (rounded up slightly for knob fit) */
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--neutral-600);
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 11px;
  /* 14px * 0.8 ~= 11px */
  width: 11px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: .4s;
}

input:checked+.slider {
  background-color: var(--primary-500);
}

input:focus+.slider {
  box-shadow: 0 0 1px var(--primary-500);
}

input:checked+.slider:before {
  transform: translateX(11px);
  /* Travel distance adjusted */
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

