/* --- Main Menu Overlay --- */
.main-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  /* Ensure it stays on top */
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.main-menu.hidden,
.level-overlay.hidden {
  opacity: 0;
  pointer-events: none;
}

.level-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  /* Slightly transparent */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.menu-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  text-align: center;
}

.menu-icon {
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
}

.menu-title {
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 3rem;
  font-weight: 500;
  color: var(--radar-white, #e0e0e0);
  margin: 0;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.menu-buttons {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  max-width: 300px;
}

.menu-button {
  background: transparent;
  border: 1px solid var(--radar-green, #0f0);
  color: var(--radar-green, #0f0);
  font-family: 'IBM Plex Mono', monospace;
  font-size: 1.25rem;
  padding: 1rem 2rem;
  cursor: pointer;
  text-transform: uppercase;
  transition: all 0.2s ease;
  width: 100%;
}

.menu-button:hover {
  background: rgba(0, 255, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
}

.menu-button:active {
  background: rgba(0, 255, 0, 0.2);
  transform: translateY(2px);
}

